import React, {Component} from 'react'
import PropTypes from 'prop-types';
import Tabs from '@mui/material/Tabs';
import Tab from '@mui/material/Tab';
import Typography from '@mui/material/Typography';
import Box from '@mui/material/Box';
import Dropdown from "./dropdown";
import Favorite from "./favorite";


function TabPanel(props) {
    const { children, value, index, ...other } = props;
  
    return (
      <div
        role="tabpanel"
        hidden={value !== index}
        id={`simple-tabpanel-${index}`}
        aria-labelledby={`simple-tab-${index}`}
        {...other}
      >
        {value === index && (
          <Box sx={{ p: 3 }}>
              {/* typhography <div> cannot appear as a descendant of <p>   component={'div'} variant={'body2'} */}
            <Typography component={'div'} variant={'body2'}>{children}</Typography>
          </Box>
        )}
      </div>
    );
  }
  
  TabPanel.propTypes = {
    children: PropTypes.node,
    index: PropTypes.number.isRequired,
    value: PropTypes.number.isRequired,
  };
  
  function a11yProps(index) {
    return {
      id: `simple-tab-${index}`,
      'aria-controls': `simple-tabpanel-${index}`,
    };
  }
  
  export default function BasicTabs() {
    const [value, setValue] = React.useState(0);
  
    const handleChange = (event, newValue) => {
      setValue(newValue);
    };
  
    return (
        <>
        <Box sx={{ width: '100%' }}>
            <Box sx={{  }} className="tab-menu">
                <Tabs value={value} onChange={handleChange} aria-label="  " className='tab-menu-button'>
                    <Tab label="" {...a11yProps(0)} />
                    <Tab label="" {...a11yProps(1)} />
                </Tabs>
            </Box>
            <div className='tab-panel'>
                <TabPanel value={value} index={0}>
                    <Dropdown />
                </TabPanel>
                <TabPanel value={value} index={1}>
                    <Favorite />
                </TabPanel>
            </div>
        </Box>
        </>
    );
  }