import React, {Component} from 'react'
import ExpandMoreIcon from '@mui/icons-material/ExpandMore';
import styled from "styled-components";
import Link from '@mui/material/Link';
import ToggleButton from '@mui/material/ToggleButton';
import ToggleButtonGroup from '@mui/material/ToggleButtonGroup';

const Dlist = styled.dl`
    color: #c2c9d6;
`
const Dtitle = styled.dt`
    margin-bottom: 15px;
    padding: 2px 0 8px 20px;
    border-bottom: 1px solid #464e65;
    font-size: 16px;
    line-height: 30px;
    color:#fff;
`
const Dcontents = styled.dd`
    padding-left: 20px;
    font-size: 16px;
    line-height: 30px;
`

export default function ControlledAccordions() {

    const [favorites, setDevices] = React.useState(() => ['favorite1_01', 'favorite1_02', 'favorite1_03']);
    const handleDevices = (event, newDevices) => {
        if (newDevices.length) {
                setDevices(newDevices);
            }
        };

    return (
    <div className="menu-accordion">
        <Dlist>
            <Dcontents className="flex-box">
                <Link></Link>
                <ToggleButtonGroup
                    value={favorites}
                    onChange={handleDevices}
                    aria-label="favorite"
                    >
                    <ToggleButton value="favorite1_01" aria-label="favorite1_01" >
                            
                    </ToggleButton>
                    </ToggleButtonGroup>
            </Dcontents>
            <Dcontents className="flex-box">
                <Link> </Link>
                <ToggleButtonGroup
                    value={favorites}
                    onChange={handleDevices}
                    aria-label="favorite"
                    >
                <ToggleButton value="favorite1_02" aria-label="favorite1_02">
                        
                </ToggleButton>
                </ToggleButtonGroup>
            </Dcontents>
            <Dcontents className="flex-box">
                <Link> </Link>
                <ToggleButtonGroup
                    value={favorites}
                    onChange={handleDevices}
                    aria-label="favorite"
                    >
                <ToggleButton value="favorite1_03" aria-label="favorite1_03">
                        
                </ToggleButton>
                </ToggleButtonGroup>
            </Dcontents>
        </Dlist>
    </div>
    );
}
